const FormHelpers = {
    handleBlur : (event, data, setData) => {
        const fieldName = event.target.name;
        const newField = {...data[fieldName]};
        newField.touched = true
        if(data[fieldName].isRequired) {
            if(!data[fieldName].value.length) {
                newField.displayMsg = "Ce champs est requis"
            } else {
                if(data[fieldName].isValid) {
                    newField.displayMsg = false
                } else {
                    newField.displayMsg = newField.errorMsg
                }
            }
        } else if(!data[fieldName].isRequired) {
            if(!data[fieldName].isRequired && data[fieldName].value && !data[fieldName].isValid) {
                newField.displayMsg = newField.errorMsg
            }
        }
        setData({...data, [fieldName] : newField});
    },
    handleChange : (event, data, setData) => {
        const fieldName = event.target.name;
        const fieldValue = event.target.value;
        const newField = {...data[fieldName]};
        newField.value = fieldValue
        if(data[fieldName].regexToTest.test(fieldValue)) {
            newField.isValid = true
            newField.displayMsg = false
        } else {
            newField.isValid = false
            if(!newField.value && newField.isRequired) {
                newField.displayMsg = "Ce champs est requis"
            } else if(newField.value) {
                newField.displayMsg = newField.errorMsg
            } else {
                newField.displayMsg = false
            }
        }
        setData({...data, [fieldName] : newField});
    },
    handleValidation : (history) => {
        alert("Vous êtes bien connecté")
        history.push("accueil")
    },
    checkRequired : (fieldsToCheck) => {
        if((fieldsToCheck.filter(key => 
            key.isRequired).every(val => 
                val.isValid))) {
                    return true
        }
    },
    checkNotRequired : (fieldsToCheck) => {
        if((fieldsToCheck.filter(key => 
            !key.isRequired).every(val => 
                !val.value || (val.value && val.isValid)))) {
                    return true
        }
    },
    allowValidation : (fieldsToCheck) => {
        if(FormHelpers.checkRequired(fieldsToCheck) && FormHelpers.checkNotRequired(fieldsToCheck)) {
            return true
        }
    },
    handleExistingEmail : (email, data, setData) => {
        const newField = {...data.mail};
        newField.isValid = false
        newField.displayMsg = `${email} : cette adresse est déja rattachée à un compte`
        setData({...data, mail : newField});
    }
}

export default FormHelpers;