import React from 'react';
import "./account.css";
import { useHistory } from 'react-router-dom';

const AccountRouter = (props) => {

    const history = useHistory()

    const handleRoute = (url) => {
        history.push(`account/${url}`)
    }
    
    if(!props.online) {
        return (
            <>
                <h3>Se connecter</h3>
                <div className='accountButton' onClick={() => handleRoute("connexion")}>Vous avez déjà un compte</div>
                <div className='accountButton' onClick={() => handleRoute("signIn")}>Créer un compte</div>
            </>
        )
    } else {
        return (
            <>
                <h3>Bonjour {props.online.name}, {props.online.gender === 1 ? "vous êtes bien connecté : )" : "vous êtes bien connectée : )"}</h3>
                <img src={`${`${process.env.PUBLIC_URL}/Accessories/A1073.jpg`}`} style={{borderStyle: "double", marginTop: "20px"}}></img><br></br>
                <div className='accountButton' onClick={() => props.handleAccountOnline(false)}>Se déconnecter</div>
            </>
        )
    }
}

export default AccountRouter;