import React, { useState } from 'react';
import "./account.css";
import Title from './title';
import AccountRouter from './accountRouter';

const Account = (props) => {

    return (
        <div id="displayWrapper">
            <div id="displayAccount">
                <Title
                    css = "displayTitle"
                >IDENTIFICATION</Title>
                {!props.online ? props.subComponent : 
                    <AccountRouter
                        online={props.online}
                        handleAccountOnline={props.handleAccountOnline}
                    ></AccountRouter>}
            </div>
        </div>
    )
}

export default Account;