import React, { useEffect, useState } from 'react';
import FormHelpers from './formHelpers';
import FormFields from './formFields';
import { useHistory } from 'react-router-dom';
import FormTextInput from './formTextInput';

const AccountCreation = (props) => {

    const history = useHistory()
    const {mail, password, name, surname, phone, birth, passwordConfirmation} = FormFields
    const [signInData, setSignInData] = useState({
        mail, password, name, surname, phone, birth, passwordConfirmation
    })
    const fieldsToCheck = [signInData.name, signInData.surname, signInData.mail, signInData.phone, signInData.birth, signInData.password, signInData.passwordConfirmation]
    const inputList = []
    const newCustomer = {
        mail : signInData.mail.value,
        password : signInData.password.value,
        surname : signInData.surname.value,
        phone : signInData.phone.value,
        birth: signInData.birth.value,
        name: signInData.name.value
    }

    useEffect(() => {//cette fonction ne se lance qu'au début
        fetch("http://localhost:3001/customerBase/")
        .then(response =>
            console.log()
        )
    }, [])

    const createNewCustomer = (customer) => {
        FormHelpers.handleValidation(history)
        return fetch("http://localhost:3001/customerBase/", {
          method: "POST",
          body: JSON.stringify(customer),
          headers : {"Content-Type" : "application/json"}
      })
        .then(response => response.json())
        .catch(error => console.log(error));
    }

    useEffect(() => {
        checkExistingMail(signInData.mail.value)
    }, [signInData.mail.isValid && signInData.mail.touched])

    const checkExistingMail = (mail) => {
        return fetch(`http://localhost:3001/customerBase?mail=${mail}`, {
          method: "GET",
          headers : {"Content-Type" : "application/json"}
      })   
        .then(response => {
            response.json()
            .then(data => {
                data.length > 0 && FormHelpers.handleExistingEmail(mail, signInData, setSignInData)
            });
        })
        .catch(error => console.log(error));
    }

    fieldsToCheck.map((field, index) => {
        inputList.push(
            <FormTextInput 
                key={index}
                field={field}
                data={signInData}
                setData={setSignInData}
            ></FormTextInput>
        )
    })

    return (
        <>
            <h3>CREATION DU COMPTE</h3>
            <div id="formContainer" style={{textAlign: "left"}}>
                {inputList}
            </div> 
            <div className={
                    FormHelpers.allowValidation(fieldsToCheck) && 
                    signInData.password.value === signInData.passwordConfirmation.value ? 
                    'accountButton' : 
                    'validationButtonGrey'
                } 
                onClick={() => 
                    FormHelpers.allowValidation(fieldsToCheck) && 
                    signInData.password.value === signInData.passwordConfirmation.value && 
                    createNewCustomer(newCustomer)
                }
            >Je crée mon compte</div>
        </>
    )
            
}

export default AccountCreation;