import React from 'react';
import FormHelpers from './formHelpers';

const FormTextInput = (props) => {
    return (
        <>
        <div className='formTextInput'>
            <label htmlFor={props.field.name}>{props.field.label} : {!props.data[props.field.name].isRequired && <i>- falcultatif -</i>}</label><br></br>
            <input 
                id={props.field.name}
                type="text"
                name={props.field.name}
                onBlur={(e) => FormHelpers.handleBlur(e, props.data, props.setData)} 
                onChange={(e) => FormHelpers.handleChange(e, props.data, props.setData)}
            ></input><span className='spanValidation'>{props.data[props.field.name].isValid && " ✓"}</span><br></br>
            <span className='spanDisplayMsg'>{props.data[props.field.name].touched && props.data[props.field.name].displayMsg}</span><br></br>
        </div>
        </>
    )
}

export default FormTextInput;