import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import FormHelpers from './formHelpers';
import FormFields from './formFields';
import FormTextInput from './formTextInput';

const AccountConnexion = (props) => {

    const history = useHistory()
    const {mail, password} = FormFields
    const [connexionData, setConnexionData] = useState({
        mail, password
    })
    const fieldsToCheck = [connexionData.mail, connexionData.password]
    const inputList = []

    fieldsToCheck.map((field, index) => {
        inputList.push(
            <FormTextInput 
                key={index}
                field={field}
                data={connexionData}
                setData={setConnexionData}
            ></FormTextInput>)
    })

    return (
        <>
        <h3>CONNEXION AU COMPTE</h3>
        <div id="formContainer" style={{textAlign: "left"}}>
            {inputList}
        </div>
        <div className={FormHelpers.allowValidation(fieldsToCheck) ? 'accountButton' : 'validationButtonGrey'} onClick={() => FormHelpers.allowValidation(fieldsToCheck) && FormHelpers.handleValidation(history)}>Me connecter</div>
        </>
    )
                
}

export default AccountConnexion;